.normal-player {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 150;
  background-color: rgba(0, 0, 0, 0.5);
}
.normal-player .background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.normal-player .background:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.normal-player .background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(20px);
  transform: scale(1.1);
}
.normal-player .top {
  position: relative;
  margin-bottom: 25px;
  color: #fff;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
}
.normal-player .top .back {
  position: absolute;
  height: 40px;
  width: 60px;
  line-height: 40px;
}
.normal-player .top .back span {
  font-size: 22px;
}
.normal-player .top h2 {
  height: 40px;
  width: 70%;
  line-height: 40px;
  font-size: 18px;
  margin: 0 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 700;
}
.normal-player .top h3 {
  height: 20px;
  line-height: 20px;
  font-size: 15px;
  font-weight: 700;
}
.normal-player .middle {
  position: fixed;
  top: 80px;
  bottom: 170px;
  display: flex;
  width: 200%;
  text-align: center;
}
.normal-player .middle .middle-left {
  width: 100%;
}
.normal-player .middle .middle-left .image-wrapper {
  width: 80%;
  margin: 0 auto;
}
.normal-player .middle .middle-left .image-wrapper img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 10px solid hsla(0, 0%, 100%, 0.1);
  box-sizing: border-box;
}
.normal-player .middle .middle-left .playing-lyric-wrapper {
  width: 80%;
  margin: 30px auto 0;
  overflow: hidden;
  text-align: center;
}
.normal-player .middle .middle-left .playing-lyric-wrapper .playing-lyric {
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #fff;
}
.normal-player .middle .middle-right {
  width: 100%;
  height: 100%;
}
.normal-player .middle .middle-right .lyric-wrapper {
  height: 100%;
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
}
.normal-player .middle .middle-right .lyric-wrapper .text {
  line-height: 32px;
  color: rgba(255, 255, 255, 0.8);
}
.normal-player .middle .middle-right .lyric-wrapper .text.current {
  color: #ffb800;
  font-weight: bold;
}
.normal-player .bottom {
  position: fixed;
  bottom: 50px;
  width: 100%;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
}
.normal-player .bottom .dot-wrapper {
  text-align: center;
}
.normal-player .bottom .dot-wrapper .dot {
  display: inline-block;
  vertical-align: middle;
  margin: 0 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: hsla(0, 0%, 100%, 0.5);
}
.normal-player .bottom .dot-wrapper .dot.active {
  width: 20px;
  border-radius: 5px;
  background: hsla(0, 0%, 100%, 0.8);
}
.normal-player .bottom .progress-wrapper {
  display: flex;
  align-items: center;
  width: 80%;
  height: 50px;
  margin: 0 auto;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
}
.normal-player .bottom .progress-wrapper .time {
  width: 35px;
  text-align: center;
}
.normal-player .bottom .progress-wrapper .bar-wrapper {
  flex-grow: 1;
}
.normal-player .bottom .control-wrapper {
  display: flex;
  padding: 0 30px;
}
.normal-player .bottom .control-wrapper.disable div:nth-child(2),
.normal-player .bottom .control-wrapper.disable div:nth-child(3),
.normal-player .bottom .control-wrapper.disable div:nth-child(4) {
  color: rgba(255, 205, 49, 0.5);
}
.normal-player .bottom .control-wrapper div {
  width: 20%;
  height: 41px;
  line-height: 41px;
  text-align: center;
  color: #ffb800;
}
.normal-player .bottom .control-wrapper div span {
  font-size: 32px;
}
