.mini-player {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  z-index: 100;
  border-top: 1px solid #eee;
  background-color: #fff;
}
.mini-player .icon {
  width: 40px;
  height: 40px;
  padding: 0 10px 0 20px;
}
.mini-player .icon img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.mini-player .text {
  flex-grow: 1;
  overflow: hidden;
  color: #333;
  line-height: 20px;
}
.mini-player .text h2 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.mini-player .text p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
  color: #777;
}
.mini-player .control {
  width: 30px;
  padding: 0 10px;
}
.mini-player .control .iconfont {
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #108ee9;
}
.mini-player .control .iconfont2 {
  color: #108ee9;
  font-size: 24px;
}
