.container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 80vw;
  background: #f5f5f9;
  overflow: hidden;
  color: #333;
}
.container .header {
  position: relative;
  height: 28vh;
  background: #d8bcac;
}
.container .header > div {
  position: absolute;
  left: 0;
  bottom: 10px;
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 20px;
}
.container .header > div img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
  padding: 0 20px;
}
.container .contenr .section .divider {
  border-bottom: 1px solid #d3d4da;
  margin-top: 10px;
}
.container .contenr .section .title {
  height: 40px;
  color: #666;
  line-height: 40px;
  padding: 0 10px;
  margin-top: 10px;
}
.container .contenr .section ul li {
  display: flex;
  align-items: center;
  height: 45px;
  font-size: 14px;
  padding: 0 10px;
}
.container .contenr .section ul li div:first-child {
  width: 20px;
  height: 20px;
  font-weight: bold;
  font-size: 20px;
}
.container .contenr .section ul li div:nth-child(2) {
  flex-grow: 1;
  margin: 0 20px;
}
.side-up-init li {
  overflow: visible;
  transform: translate(100px, 100px);
  opacity: 0;
}
.side-up-init li:nth-child(1) {
  transition-delay: 0.08s;
}
.side-up-init li:nth-child(2) {
  transition-delay: 0.16s;
}
.side-up-init li:nth-child(3) {
  transition-delay: 0.24s;
}
.side-up-init li:nth-child(4) {
  transition-delay: 0.32s;
}
.side-up-init li:nth-child(5) {
  transition-delay: 0.4s;
}
.side-up-init li:nth-child(6) {
  transition-delay: 0.48s;
}
.side-up li {
  transform: translate(0, 0);
  transition: all 0.4s;
  opacity: 1;
}
.fade-init {
  opacity: 0;
}
.fade {
  transition: all 0.4s;
  transition-delay: 1s;
  opacity: 1;
}
