.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  transition: all 0.25s;
  font-size: 14px;
}
.container .header {
  width: 100%;
  height: 44px;
  background: #108ee9;
  color: #fff;
  line-height: 44px;
  padding: 0 20px;
}
.container .navigation-menu {
  display: flex;
  background: #108ee9;
  color: #fff;
  height: 44px;
  line-height: 44px;
  margin-top: -1px;
}
.container .navigation-menu li {
  flex-grow: 1;
  text-align: center;
}
.container .navigation-menu li a {
  display: block;
  height: 100%;
  color: #fff;
  opacity: 0.8;
  font-size: 15px;
  box-sizing: border-box;
}
.container .navigation-menu li a.active {
  opacity: 1;
  border-bottom: 3px solid #ffb800;
}
.container .content {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;
  overflow: auto;
  box-sizing: border-box;
  background: #f5f5f9;
}
.container .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 199;
}
