.progress-bar {
  height: 30px;
  width: 100%;
}
.progress-bar .bar-inner {
  position: relative;
  top: 13px;
  height: 4px;
  background: rgba(255, 255, 255, 0.3);
}
.progress-bar .bar-inner .progress {
  position: absolute;
  height: 100%;
  background: #ffb800;
}
.progress-bar .bar-inner .progress-btn-wrapper {
  position: absolute;
  left: -8px;
  top: -13px;
  width: 30px;
  height: 30px;
}
.progress-bar .bar-inner .progress-btn-wrapper .progress-btn {
  position: relative;
  top: 7px;
  left: 7px;
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  border: 3px solid #fff;
  border-radius: 50%;
  background: #ffb800;
}
